*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:#000;
  height: 100vh;
  width: 100%;
}

.container{
  width:500px;
  height:400px;
  align-items: center;
  background-color:whitesmoke;
  box-shadow: 0px 0px 10px -3px rgba(247, 243, 243, 0.8);
  padding: 10px;
}


.heading__style{
 margin: 10px 0;
 padding: 5px;
 background-color: rgb(145, 195, 238);
 text-align: center;
 width:100%;
 font-size: 1rem;
 letter-spacing: 1px;
 font-weight: 500;
 color: #001200;
}
.input-box-btn{
  margin: 10px 0;
  display: flex;
  justify-content: space-evenly;
}
input{
  outline: none;
  background-color:white;
  border: none;
  width:100%;
  text-align: center;
  margin: 0 5px;
  font-size: 1rem;
  font-weight: 500;
  text-transform: capitalize;
  padding:10px;
}
input:hover{
  box-shadow: 0 0 10px 0 rgba(0, 0 ,0,0.8);
}

.btn{
  background-color:cyan;
  border: none;
  border-radius: 50%;
}
.list-box{
  width: 100%;
  background-color: aquamarine;
  height: 60%;
  align-items: center;
  overflow: auto;
}
.list{
  list-style: none;
  font-size: 1.1rem;
  font-weight: 600;
  margin:10px 0;
}
.list-item{
  margin: 0 10px;
  color: #818188;

}
.DeleteIcon{
  margin-left: 12px;
  color: #818188;
}
.DeleteIcon:hover{
  color: red;
}
.item-box{
  display: flex;
  margin: 20px 20px;
  align-items: center;
}

@media(max-width:720px){
  .container{
    width:350px;
    height:350px;
  } 
}
